body {
    margin: 0;
    font-family: "Roboto", "Segoe UI", 
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#d0e4f7+0,73b1e7+24,0a77d5+50,539fe1+79,87bcea+100;Blue+Pipe+%231 */
    background: #d0e4f7; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
        to right,
        #d0e4f7 0%,
        #73b1e7 24%,
        #0a77d5 50%,
        #539fe1 79%,
        #87bcea 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d0e4f7', endColorstr='#87bcea',GradientType=1 ); /* IE6-9 */
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}

.p-link:hover {
  text-decoration: underline;
  cursor: pointer;
}
.Tilt {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f0f9ff+0,cbebff+47,a1dbff+100;Blue+3D+%2313 */
    background: rgb(240, 249, 255); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
        to right,
        rgba(240, 249, 255, 1) 0%,
        rgba(203, 235, 255, 1) 47%,
        rgba(161, 219, 255, 1) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0f9ff', endColorstr='#a1dbff',GradientType=1 ); /* IE6-9 */
}

.form {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#feffff+0,ddf1f9+35,a0d8ef+100;Blue+3D+%2318 */
    background: rgb(254, 255, 255); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(
        ellipse at center,
        rgba(254, 255, 255, 1) 0%,
        rgba(221, 241, 249, 1) 35%,
        rgba(160, 216, 239, 1) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#feffff', endColorstr='#a0d8ef',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

ul {
    list-style: none;
    background-color: aliceblue;
    padding: 0;
    margin: 0;
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
}

li {
    width: 100%;
}

.firstLi {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.ingredientLi {
    display: flex;
    justify-content: space-between;
}

.ingredientLi > span {
    margin-left: 2px;
    margin-right: 25px;
}

img {
    max-width: 100%;
    max-height: 100%;
}

@media only screen and (max-width: 600px) {
    img {
        max-width: 800px;
        max-height: 100%;
    }
  }




